import React, { useEffect } from 'react';
import { useHistory, useLocation } from 'react-router';
import { Card, Result, Button, Icon } from 'antd';
import './index.scss';
import { moneyToString } from '../../../utils/formatUtil';

export default () => {
  const location = useLocation()
  const history = useHistory();
  const query = new URLSearchParams(location.search);
  const requestId = query.get('requestId');
  const paymentId = query.get('paymentId');
  const amount = query.get('amount');
  const result = query.get('result');
  const resultMsg = query.get('resultMsg');

  const gotoHomePage = () => {
    history.replace('/sinh-vien')
  }

  const gotoPaymentPage = () => {
    history.replace('/hoc-phi')
  }

  useEffect(() => {
    if (!requestId || !paymentId || !amount || !result) {
      gotoHomePage()
      return
    }

    const replacedUrl = history.createHref({ pathname: location.pathname })
    window.history.replaceState(null, null, replacedUrl)
  }, [])

  if (!requestId || !paymentId || !amount || !result) {
    return null
  }

  const successCode = '1'

  return (
    <div className="payment-result">
      <Result
        status={result === successCode ? "success" : "error"}
        title={result === successCode ? "Thanh toán học phí thành công!" : "Thanh toán học phí thất bại!"}
        extra={
          <React.Fragment>
            <Card style={{ background: '#ECECEC', padding: '30px' }}>
              <div className="leaders">
                <p className="description">
                  <span className="left">Hóa đơn</span>
                  <span className="right">{paymentId}</span>
                </p>
                <p className="description">
                  <span className="left">Số tiền</span>
                  <span className="right">{moneyToString(amount)}</span>
                </p>
                <p className="description">
                  <span className="left">Kết quả</span>
                  <span className="right">{result === successCode ? "Thanh toán học phí thành công!" : "Thanh toán học phí thất bại"}</span>
                </p>
                {result !== successCode &&
                  <p className="description">
                    <span className="left">Thông báo</span>
                    <span className="right">{resultMsg}</span>
                  </p>
                }
              </div>
            </Card>
            <Button.Group>
              <Button type="dashed" onClick={gotoHomePage} >
                <Icon type="home" />
                Trang chủ
              </Button>
              <Button type="dashed" onClick={gotoPaymentPage} >
                <Icon type="credit-card" />
                Trang thanh toán
              </Button>
            </Button.Group>
          </React.Fragment>
        }
      />
    </div>
  )
}


